import React from 'react';
import { BrowserRouter as Router, Routes, Route } from 'react-router-dom';
import { Provider } from 'react-redux';
import store from './store/store/store'; // Import the store
import Navbar from './components/Navbar';
import Home from './pages/Home';
import Register from './pages/Register';
import Login from './pages/Login';
import Profile from './pages/UserProfile';
import RideRequests from './pages/RideRequest';
import RideOffers from './pages/RideOffer';
import AdminDashboard from './pages/AdminDashboard';

function App() {
  return (
    <Provider store={store}> 
      <Router>
        <div>
          <Navbar />
          <Routes>
            <Route path="/" element={<Home />} />
            <Route path="/register" element={<Register />} />
            <Route path="/login" element={<Login />} />
            <Route path="/profile" element={<Profile />} />
            <Route path="/ride-requests" element={<RideRequests />} />
            <Route path="/ride-offers" element={<RideOffers />} />
            <Route path="/admin" element={<AdminDashboard />} />
          </Routes>
        </div>
      </Router>
    </Provider>
  );
}

export default App;
