// src/reducers/authReducer.js
import {
    REGISTER_USER,
    LOGIN_USER,
    LOGOUT_USER,
    SET_USER_PROFILE
  } from '../actions/actionTypes';
  
  const initialState = {
    user: null,
    token: null,
  };
  
  const authReducer = (state = initialState, action) => {
    switch (action.type) {
      case REGISTER_USER:
      case LOGIN_USER:
        return {
          ...state,
          user: action.payload.user,
          token: action.payload.token,
        };
      case LOGOUT_USER:
        return {
          ...state,
          user: null,
          token: null,
        };
      case SET_USER_PROFILE:
        return {
          ...state,
          user: action.payload,
        };
      default:
        return state;
    }
  };
  
  export default authReducer;
  