// src/actions/rideActions.js
import {
    SET_RIDES,
    SET_RIDE_REQUESTS,
    SET_RIDE_OFFERS
  } from './actionTypes';
  import {
    createRideRequest as apiCreateRideRequest,
    createRideOffer as apiCreateRideOffer,
    fetchRides as apiFetchRides,
    fetchRideRequests as apiFetchRideRequests,
    fetchRideOffers as apiFetchRideOffers
  } from '../../services/apiService';
  import {
    emitNewRideRequest,
    emitNewRideOffer
  } from '../../services/socketService';
  
  export const createRideRequest = (data) => async (dispatch, getState) => {
    const token = getState().auth.token;
    try {
      const response = await apiCreateRideRequest(data, token);
      dispatch(fetchRides());
      emitNewRideRequest(response.data);
    } catch (error) {
      console.error('Create Ride Request Error:', error);
    }
  };
  
  export const createRideOffer = (data) => async (dispatch, getState) => {
    const token = getState().auth.token;
    try {
      const response = await apiCreateRideOffer(data, token);
      dispatch(fetchRides());
      emitNewRideOffer(response.data);
    } catch (error) {
      console.error('Create Ride Offer Error:', error);
    }
  };
  
  export const fetchRides = () => async (dispatch, getState) => {
    const token = getState().auth.token;
    try {
      const response = await apiFetchRides(token);
      dispatch({ type: SET_RIDES, payload: response.data });
    } catch (error) {
      console.error('Fetch Rides Error:', error);
    }
  };
  
  export const fetchRideRequests = () => async (dispatch, getState) => {
    const token = getState().auth.token;
    try {
      const response = await apiFetchRideRequests(token);
      dispatch({ type: SET_RIDE_REQUESTS, payload: response.data });
    } catch (error) {
      console.error('Fetch Ride Requests Error:', error);
    }
  };
  
  export const fetchRideOffers = () => async (dispatch, getState) => {
    const token = getState().auth.token;
    try {
      const response = await apiFetchRideOffers(token);
      dispatch({ type: SET_RIDE_OFFERS, payload: response.data });
    } catch (error) {
      console.error('Fetch Ride Offers Error:', error);
    }
  };
  

  