import React, { useState } from 'react';
import { useDispatch } from 'react-redux';
import { createRideOffer } from '../store/actions/rideActions';

const RideOffer = ({ history }) => {
  const dispatch = useDispatch();
  const [form, setForm] = useState({
    pickupLocation: '',
    destination: '',
    dateTime: '',
    seats: ''
  });

  const handleChange = (e) => {
    const { name, value } = e.target;
    setForm(prevForm => ({ ...prevForm, [name]: value }));
  };

  const handleSubmit = (e) => {
    e.preventDefault();
    dispatch(createRideOffer(form));
    history.push('/rides');
  };

  return (
    <div>
      <h2>Offer a Ride</h2>
      <form onSubmit={handleSubmit}>
        <div>
          <label>Pickup Location: </label>
          <input
            type="text"
            name="pickupLocation"
            value={form.pickupLocation}
            onChange={handleChange}
          />
        </div>
        <div>
          <label>Destination: </label>
          <input
            type="text"
            name="destination"
            value={form.destination}
            onChange={handleChange}
          />
        </div>
        <div>
          <label>Date and Time: </label>
          <input
            type="datetime-local"
            name="dateTime"
            value={form.dateTime}
            onChange={handleChange}
          />
        </div>
        <div>
          <label>Seats: </label>
          <input
            type="number"
            name="seats"
            value={form.seats}
            onChange={handleChange}
          />
        </div>
        <button type="submit">Offer Ride</button>
      </form>
    </div>
  );
};

export default RideOffer;
