// src/reducers/rideReducer.js
import {
    SET_RIDES,
    SET_RIDE_REQUESTS,
    SET_RIDE_OFFERS
  } from '../actions/actionTypes';
  
  const initialState = {
    rides: [],
    requests: [],
    offers: [],
  };
  
  const rideReducer = (state = initialState, action) => {
    switch (action.type) {
      case SET_RIDES:
        return {
          ...state,
          rides: action.payload,
        };
      case SET_RIDE_REQUESTS:
        return {
          ...state,
          requests: action.payload,
        };
      case SET_RIDE_OFFERS:
        return {
          ...state,
          offers: action.payload,
        };
      default:
        return state;
    }
  };
  
  export default rideReducer;
  