// src/actions/authActions.js
import {
    REGISTER_USER,
    LOGIN_USER,
    LOGOUT_USER,
    SET_USER_PROFILE
  } from './actionTypes';
  import {
    register,
    login,
    fetchUserProfile as apiFetchUserProfile,
    updateUserProfile as apiUpdateUserProfile
  } from '../../services/apiService';
  
  export const registerUser = (user) => async dispatch => {
    try {
      const response = await register(user);
      dispatch({ type: REGISTER_USER, payload: response.data });
    } catch (error) {
      console.error('Register User Error:', error);
    }
  };
  
  export const loginUser = (user) => async dispatch => {
    try {
      const response = await login(user);
      dispatch({ type: LOGIN_USER, payload: response.data });
    } catch (error) {
      console.error('Login User Error:', error);
    }
  };
  
  export const logoutUser = () => ({ type: LOGOUT_USER });
  
  export const fetchUserProfile = () => async (dispatch, getState) => {
    const token = getState().auth.token;
    try {
      const response = await apiFetchUserProfile(token);
      dispatch({ type: SET_USER_PROFILE, payload: response.data });
    } catch (error) {
      console.error('Fetch User Profile Error:', error);
    }
  };
  
  export const updateUserProfile = (data) => async (dispatch, getState) => {
    const token = getState().auth.token;
    try {
      const response = await apiUpdateUserProfile(data, token);
      dispatch({ type: SET_USER_PROFILE, payload: response.data });
    } catch (error) {
      console.error('Update User Profile Error:', error);
    }
  };
  