// src/services/apiService.js
import axios from 'axios';

const API_URL = 'http://localhost:5000/api';

export const register = (user) => axios.post(`${API_URL}/auth/register`, user);
export const login = (user) => axios.post(`${API_URL}/auth/login`, user);

export const fetchUserProfile = (token) => axios.get(`${API_URL}/profile`, {
  headers: { Authorization: `Bearer ${token}` }
});
export const updateUserProfile = (data, token) => axios.put(`${API_URL}/profile`, data, {
  headers: { Authorization: `Bearer ${token}` }
});

export const createRideRequest = (data, token) => axios.post(`${API_URL}/rides/requests`, data, {
  headers: { Authorization: `Bearer ${token}` }
});
export const createRideOffer = (data, token) => axios.post(`${API_URL}/rides/offers`, data, {
  headers: { Authorization: `Bearer ${token}` }
});
export const fetchRides = (token) => axios.get(`${API_URL}/rides`, {
  headers: { Authorization: `Bearer ${token}` }
});
export const fetchRideRequests = (token) => axios.get(`${API_URL}/rides/requests`, {
  headers: { Authorization: `Bearer ${token}` }
});
export const fetchRideOffers = (token) => axios.get(`${API_URL}/rides/offers`, {
  headers: { Authorization: `Bearer ${token}` }
});
export const fetchUsersAction = (token) => axios.get(`${API_URL}/admin/users`, {
  headers: { Authorization: `Bearer ${token}` }
});
