import React, { useEffect } from 'react';
import { useDispatch, useSelector } from 'react-redux';
// Correct import statement
import { fetchUsers } from '../store/actions/adminActions';
import { fetchRideRequests, fetchRideOffers } from '../store/actions/rideActions';


const AdminDashboard = () => {
  const dispatch = useDispatch();
  const users = useSelector(state => state.users);
  const rideRequests = useSelector(state => state.rideRequests);
  const rideOffers = useSelector(state => state.rideOffers);

  useEffect(() => {
    dispatch(fetchUsers());
    dispatch(fetchRideRequests());
    dispatch(fetchRideOffers());
  }, [dispatch]);

  return (
    <div>
      <h2>Admin Dashboard</h2>
      <div>
        <h3>Users</h3>
        <ul>
          {users.map(user => (
            <li key={user.id}>{user.username} - {user.email}</li>
          ))}
        </ul>
      </div>
      <div>
        <h3>Ride Requests</h3>
        <ul>
          {rideRequests.map(request => (
            <li key={request.id}>{request.pickupLocation} to {request.destination}</li>
          ))}
        </ul>
      </div>
      <div>
        <h3>Ride Offers</h3>
        <ul>
          {rideOffers.map(offer => (
            <li key={offer.id}>{offer.pickupLocation} to {offer.destination}</li>
          ))}
        </ul>
      </div>
    </div>
  );
};

export default AdminDashboard;
